/* header */
.header {
  width: 100%;
  min-width: 1200px;
  height: 70px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
  display: flex;
  justify-content: space-between;
}

.logo {
  width: 70px;
  margin-left: 50px;
}

.logo img {
  width: 100%;
}

.menu_ul {
  height: 70px;
  padding-right: 30px;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 8px;
}

.menu_li {
  color: inherit;
  font-size: 20px;
  font-weight: 600;
  padding: 8px 24px;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s;
}

.menu_li:hover  {
  background: #042e54;
  color: #fff;
}

section {
  width: 100%;
  height: 100vh;
  min-height: 780px;
  position: relative;
  background: #f1f1f1;
}
section:nth-child(odd) {
  background: #f0f1f9;
}

/* Main sec1 */
.sec_1 { 
  background: #000;
  color: #fff;
}

.sec_1_txt {
  width: 100%;
  height: 105px;
  font-size: 58px;
  /* font-style: italic; */
  color: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.sec_1_txt_wrap {
  width: 420px;
  height: 105px;
  position: relative;
}

.sec_1_line {
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 3px;
}
.sec_1_sub_txt {
  font-family: 'Nanum Pen Script';
  font-size: 88px;
  font-style: normal;
  color: #fff;
  text-align: center;
  opacity: 0;
  transition: opacity 0.7s;
}


/* Main sec2 */
.basic_info {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.info_img {
  width: 320px;
  height: 500px;
  position: relative;
}

.info_pan {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #e2e7eb;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

@keyframes back_pan {
  0% {width: 100px;height: 100px; border-radius: 50%; transform: translate(-130%,-200%) scale(0.2); box-shadow: 0 0 0 0}

  25% {width: 100px;height: 100px; border-radius: 50%; transform: translate(-50%,-50%) scale(1); box-shadow: 0 0 0 0}

  40% {width: 100px;height: 100px; border-radius: 50%; transform: translate(-50%,-120%) scale(1); box-shadow: 0 0 0 0}

  50% {width: 100px;height: 100px; border-radius: 50%; transform: translate(-50%,-50%) scale(1); box-shadow: 0 0 0 0}

  85% {width: 300px;height: 450px; border-radius: 20px; transform: translate(-50%,-50%) scale(1); box-shadow: 0 0 0 0}

  100% {width: 300px;height: 450px; border-radius: 20px; transform: translate(-50%,-50%);box-shadow: -10px 10px 0 0 #949494;}

}

.info_outer_cir {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background: #949494;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%) scale(0);
}

.info_inner_cir {
  width: 220px;
  height: 220px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  overflow: hidden;
}

@keyframes circle_big {
  0% {transform: translate(-50%,-50%) scale(0)}

  100% {transform: translate(-50%,-50%)scale(1);}

}

.info_inner_img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,60%);
}

@keyframes img_up {
  0% {transform: translate(-50%,60%); }

  100% {transform: translate(-50%,-20%) scale(1.4);}

}

.info_inner_img img {
  width: 55%;
}

.info_desc {
  width: calc(100% - 320px);
  padding: 24px 24px 24px 48px;
}

.info_row {
  font-size: 18px;
}

.info_title {
  
  font-weight: 600;
  padding: 8px 0;
  display: flex;
  justify-content: space-between;
}

.info_title p:first-child {
  font-size: 18px;
}

.more_box {
  margin: 0 24px 0 48px;
  cursor: pointer;
}

.more_box:hover {
  opacity: 0.6;
}

.info_detail_box {
  height: 58px;
  max-height: 200px;
  line-height: 1.5;
  padding: 12px 18px;
  margin: 12px 0;
  border-radius: 16px;
  background: #fff;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.info_row a:hover {
  opacity: 0.6;
}

/* Skills */
.skills_main {
  display: flex;
  flex-wrap: wrap;
  gap: 48px;
}

.common_skill {
  width: calc((100% - (48px * 2)) / 3);
  box-shadow: 0 0 12px 0 rgba(0, 0, 0,0.1);
  padding: 24px;
  background: #Fff;
  border-radius: 16px;
}

.basic_skill {
  /* width: 100%; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
}

.basic_skill li {
  width: 80px;
  height: 80px;
}

.basic_skill li:first-child {
  width: 100%;
  height: 25px;
}

.basic_skill img {
  width: 100%;
}

.communication_skill h3,
.version_skill h3,
.certic_skill h3 {
  margin-bottom: 32px;
}

.communication_skill img {
  width: 80px;
}

.version_skill img {
  width: 70%;
}

.certic_skill {
  width: 100%;
}

.certic_skill p {
  font-size: 18px;
  font-weight: 600;
}

/* Publishing */

.works_container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  overflow: hidden;
}

.publish_wrap { 
  height: auto;
  padding: 150px 0;
}

/* Main sec3 */
.sec_deatil_container {
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,0.5) !important;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 8;
}

.closed_box {
  position: absolute;
  right: 3%;
  top: 11%;
}

.closed_box:hover {
  opacity: 0.6;
}

.closed_box a {
  font-size: 20px;
  padding: 0 8px;
}

.sec_box {
  width: 100%;
  height: 80%;
  min-height: 750px;
  padding: 24px;
  background: #fff;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  gap: 24px;
}

.container_L {
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.desk {
  width: 100%;
  height: 100%;
  
  overflow: hidden; 
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 12px;
}

.container_R {
  width: 50%;
  padding: 16px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.c_top {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.c_content {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.c_top .c_content:last-child .c_desc{
  width: 100%;
  line-height: 1.5;
}

.c_title {
  width: 70px;
  font-size: 18px;
  text-align: center;
  padding: 8px 0;
  background: #e2e7eb;
  border-radius: 8px;
}

.c_desc {
  width: calc(100% - 150px - 32px);
  font-size: 18px;
  padding: 8px 16px;
}

.c_skills {
  width: 100%;
  display: flex;
  gap: 16px;
}

.c_skills .deail_skill { 
  width: 48px;
  height: 48px;
  border-radius: 50%;
  box-shadow: 0 0 8px 0 rgba(0,0,0,0.5);
}

.c_goToWeb {
  width: 100%;
  line-height: 1.5;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  border-radius: 16px;
  box-shadow: 0 0 8px 0 rgba(0,0,0,0.3);
}

.c_goToWeb:hover {
  background: #042e54;
  color:#fff;
}

.c_goToWeb a {
  width: 100%;
  height: 100%;
  padding: 12px 0;
  display: block;
}

/* Footer */
.footer {
  width: 100%;
  padding: 120px 0 ;
  background: #e2e7eb;
}

.contact {
  width: 100%;
  /* text-align: center; */
  display: flex;
  gap: 48px;
}

 .f_item {
  width: calc((100% - (24px * 3) )/ 4);
  box-shadow: 0 0 12px 0 rgba(0, 0, 0,0.1);
  padding: 24px;
  background: #Fff;
  border-radius: 16px;
}

.f_item:hover {
  background: #042e54;
}

.f_item:hover h1,
.f_item:hover h3 {
  color: #fff;
}

.f_item h1 {
  margin-bottom: 16px;
}

.email a{ 
  width: 100%;
  height: 100%;
  display: block;
}
